import '../App.css';
import { useState, useEffect } from "react";
import OpenAI from "openai";


function Chat() {
  const [messages, setMessages] = useState([]);
  const [inputValue, setInputValue] = useState('');
  const [isLoading, setIsLoading] = useState(false);

  useEffect(() => {
    if (messages.length === 0) {
      setMessages([{ text: "Hello, how can I help you?", sender: "ai" }]);
    }
  }, []);

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!inputValue.trim() || isLoading) return;

    setMessages(prev => [...prev, { text: inputValue, sender: "user" }]);
    setInputValue("");
    setIsLoading(true);

    try {
      //引入openai的api 
      const openai = new OpenAI({
        baseURL: process.env.REACT_APP_API_BASE_URL,
        apiKey: process.env.REACT_APP_API_KEY, // 新测试Key
        dangerouslyAllowBrowser: true
      });
      
      // 选ai模型
      const response = await openai.chat.completions.create({
        model: "gpt-3.5-turbo",  // 使用最新模型名称
        messages: [
          { role: "user", content: inputValue },
        ],
      });

      setMessages(prev => [
        ...prev, 
        { text: response.choices[0].message.content, sender: "ai" }
      ]);
    } catch (error) {
      console.error("API Error:", error); // 打印详细错误
      setMessages(prev => [
        ...prev, 
        { 
          text: `Error: ${error.message}`, // 显示具体错误信息
          sender: "ai" 
        }
      ]);
    } finally {
      setIsLoading(false);
    }
  };


return (
    <div className="chat-container">
    <div className="messages-container">
        {messages.map((msg, index) => (
        <div key={index} className={`message ${msg.sender}`}>
            {msg.text}
        </div>
        ))}
        {isLoading && <div className="message ai">loading...</div>}
      </div>
      <form onSubmit={handleSubmit} className="message-form">
        <input
          type="text"
          value={inputValue}
          onChange={(e) => setInputValue(e.target.value)}
          placeholder="Send your message here..."
        />
        <button type="submit" disabled={isLoading || !inputValue.trim()}>
          send
        </button>
      </form>
    </div>
  );
}

export default Chat;