import React from "react";
import '../App.css';
import logo from '../logo.svg';

function header() {
    return (
    <div className="App">
    <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>
            ChatBot
        </p>
        </header>
    </div>  )}

export default header;